import { getMyUserInfo, deleteMessageRecord } from "@/common/storageFunctions";
import { USER_INFO, TOKEN } from "@/common/storageKeys";
import { REFRESH_USERINFO } from "@/common/globalMsgKeys";
/**
 * setting 设置界面
 * @description 设置界面
 * @Author: 穆兰
 * @Date: 2022/1/7
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/9
 */
export default {
    data() {
        return {
            userInfo: {}
        };
    },
    async onLoad() {
        this.userInfo = await getMyUserInfo();
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            console.log("onNarLeftClick()");
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @description 跳转到指定页面
         * @param {string} url 页面跳转的url
         */
        onNavigateTo(page_url) {
            console.log(page_url);
            uni.navigateTo({
                url: page_url
            });
        },
        /**
         * @description 点击实名认证
         */
        onRealNameAuthenticationClick() {
            if (this.userInfo.isVerify == 1) {
                uni.showToast({
                    title: "您已经完成实名认证",
                    icon: "none"
                });
                return;
            }
            this.$refs.authenticationActionSheet.open();
        },
        /**
         * @description 点击清除缓存
         */
        onClearCacheClick() {
            if (!this.userInfo.id) {
                uni.showToast({
                    title: "您还未登录，请先登录",
                    icon: "error"
                });
                return;
            }
            let _this = this;
            uni.showModal({
                title: '清除聊天记录吗',
                content: "你确定要清除所有本地的聊天记录吗",
                success: function (res) {
                    if (res.confirm) {
                        deleteMessageRecord(_this.userInfo.id);
                        uni.showToast({
                            title: '清除成功'
                        });
                    }
                }
            });
        },
        /**
         * @description 登出
         */
        onLogoutClick() {
            uni.showModal({
                title: '退出登录',
                content: "你确定要退出登录吗？",
                success: function (res) {
                    if (res.confirm) {
                        uni.setStorage({ key: TOKEN, data: null });
                        uni.setStorage({ key: USER_INFO, data: null });
                        uni.$emit(REFRESH_USERINFO, { needRefresh: true });
                        uni.navigateBack({
                            delta: 1
                        });
                    }
                }
            });
        }
    }
};
